import React, { useState } from 'react';
import styles from './ExpenseForm.module.css'; // css

export default function ExpenseForm(props) {
	const [enteredTitle, setEnteredTitle] = useState('');
	const [enteredAmount, setEnteredAmount] = useState('');
	const [enteredDate, setEnteredDate] = useState('');
	// 2nd option - All states in one object
	// const [userInput, setUserInput] = useState({
	// 	enteredTitle: '',
	// 	enteredAmount: '',
	// 	enteredDate: '',
	// });

	// Helper function that stores entered title text into state(enteredTitle) --> so later we can use it
	// @event = object from which we get current text value, it is always a string type
	const titleChangeHandler = (event) => {
		setEnteredTitle(event.target.value);
		// 2nd option - All states in one object
		// Always use when state update depends on the previous state
		// setUserInput((prevState) => {
		// 	// ...prevState => Copy of the stateObject snapshot
		// 	// enteredTitle => updating only what should be updated
		// 	return { ...prevState, enteredTitle: event.target.value };
		// });
	};
	// Helper function that stores entered Amount into state
	const amountChangeHandler = (event) => {
		setEnteredAmount(event.target.value);
	};
	// Helper function that stores entered Date into state
	const dateChangeHandler = (event) => {
		setEnteredDate(event.target.value);
	};

	const submitHandler = (event) => {
		event.preventDefault(); // Prevents reload of the page , when request is sent
		// Stores all the seperate states into one object
		const expenseData = {
			title: enteredTitle,
			amount: Number(enteredAmount),
			date: new Date(enteredDate), // Converts String into Date format
		};
		// Passing data object to upper level component (-->NewExpense.js) , through props object that has method. --> check Child-to-Parent component communication
		// onSaveExpenseData will Now call saveExpenseDataHandler() , defined in NewExpense.js
		props.onSaveExpenseData(expenseData);
		// Resets Input fields --> check two-way binding concept
		setEnteredTitle('');
		setEnteredAmount('');
		setEnteredDate('');
	};

	return (
		// Every form has onSubmit event , when submit button is clicked
		<form onSubmit={submitHandler}>
			<div className={styles['new-expense__controls']}>
				<div className={styles['new-expense__control']}>
					<label>Title</label>
					{/* 
                    Two-way Binding
                    allows us not just to listen onto event , but let us pass a new value back into input when event happens
                    */}
					<input type='text' value={enteredTitle} onChange={titleChangeHandler} />
				</div>
				<div className={styles['new-expense__control']}>
					<label>Amount</label>
					<input type='number' value={enteredAmount} min='0.01' step='0.01' onChange={amountChangeHandler} />
				</div>
				<div className={styles['new-expense__control']}>
					<label>Date</label>
					<input type='date' value={enteredDate} min='2019-01-01' max='2022-12-31' onChange={dateChangeHandler} />
				</div>
			</div>
			<div className={styles['new-expense__actions']}>
				<button type='button' onClick={props.onCancel}>
					Cancel
				</button>
				<button type='submit'>Add Expense</button>
			</div>
		</form>
	);
}
