// Imports
import React, { useState } from 'react';
import ExpenseForm from './ExpenseForm';
import styles from './NewExpense.module.css'; // css

export default function NewExpense(props) {
	const [toggleForm, setToggleForm] = useState(false);
	const saveExpenseDataHandler = (enteredExpenseData) => {
		const expenseData = {
			...enteredExpenseData,
			id: Math.random().toString(),
		};
		// console.log(expenseData);
		// Passing data object to upper level component (-->App.js) , through props object that has method onAddExpense.
		// that points to a	ddExpenseHandler()
		props.onAddExpense(expenseData);
		setToggleForm(false);
	};

	const displayFormHandler = () => {
		setToggleForm((prevState) => !prevState);
	};

	return (
		<div className={styles['new-expense']}>
			{!toggleForm && (
				<button type='button' onClick={displayFormHandler}>
					Add New Expense
				</button>
			)}
			{/* Child-Parent communication(passing data) through own event function */}
			{toggleForm && <ExpenseForm onSaveExpenseData={saveExpenseDataHandler} onCancel={displayFormHandler} />}
		</div>
	);
}
