import styles from './Card.module.css'; // Css import

// Card Wrapper(container) component - that generalize css look for components that will be inside
// It acts like a shell
export default function Card(props) {
	// Because it is a Custom components , we need to tell it what is supported on this component
	const classes = styles['card'] + ' ' + props.className; // classes of the shell --> props.className adds any new class to this component class list
	// Children is reserved keyword -- > It is all that is between opening & closing brackets of custom wrapper component (basically what is inside)
	return <div className={classes}>{props.children}</div>;
}
