// Imports
import React, { useState } from 'react'; // used in older projects
import Expenses from './components/Expenses/Expenses';
import NewExpense from './components/NewExpense/NewExpense';

// Array of objects , where we store our expenses
const INNITIAL_EXPENSES = [
	{
		id: 'e1', // identificator of object --> must be unique
		title: 'Mobile Charger', // expense title
		amount: 94.12, // expense price
		date: new Date(2020, 7, 14), // date of bought
	},
	{ id: 'e2', title: 'New TV', amount: 799.49, date: new Date(2021, 2, 12) },
	{
		id: 'e3',
		title: 'Car Insurance',
		amount: 294.67,
		date: new Date(2021, 2, 28),
	},
	{
		id: 'e4',
		title: 'New Desk (Wooden)',
		amount: 450,
		date: new Date(2021, 5, 12),
	},
];

// It is so called root component , the main component that is rendered
function App() {
	const [expenses, setExpenses] = useState(INNITIAL_EXPENSES);

	const addExpenseHandler = (expense) => {
		// Updating an Array / Object state based on a previous snapshot of that state
		setExpenses((prevExpenses) => [expense, ...prevExpenses]);
	};

	return (
		<div>
			<NewExpense onAddExpense={addExpenseHandler} />
			{/* Own custom made component
			 	items is passing expenses array with data */}
			<Expenses items={expenses} />
		</div>
	);

	// Underlying code , was used in older projects --> needs import  React from 'react';
	// 1. arg = type of html element (if its custom made component you put that one)
	// 2. arg = data we are passing with props
	// 3. arg = Content of the element
	// return React.createElement(
	// 	'div',
	// 	{},
	// 	React.createElement('h2', {}, "Let's get started!"),
	// 	React.createElement(Expenses, { items: expenses })
	// );
}

export default App;
