// Imports
import styles from './ExpenseItem.module.css';
import ExpenseDate from './ExpenseDate'; // Default import
import Card from '../UI/Card';
import React from 'react'; // in curly bracers we imported useState function ( it is React Hook)

// Component is basically a JS function - As parameter we pass props object ( that stores key:value pairs )
// Props allows us passing data between components , it is key concept of React
// Thanks to props we can have DYNAMIC REUSABLE components
function ExpenseItem(props) {
	// useState Hook function --> returns array [] , can have default innitial value in ()
	// title = stores current state value, setTitle = function through you update the state
	// const [title, setTitle] = useState(props.title);

	// const clickHandler = () => {
	// 	setTitle('Updated'); // Calling this Updating function , will set a new value and MOST IMPORTANTLY runs the component function again!
	// 	console.log(title); // It will show old value , but the new one is scheduled
	// };

	// Return can have only 1 root div element
	return (
		<li>
			<Card className={styles['expense-item']}>
				<ExpenseDate date={props.date} /> {/* passing date from props to ExpenseDate component */}
				<div className={styles['expense-item__description']}>
					<h2>{props.title}</h2>
					<div className={styles['expense-item__price']}>€{props.amount}</div>
				</div>
			</Card>
		</li>
	);
}

// Exporting it to another file
export default ExpenseItem;
